// src/components/common/UI/GroupListCard.tsx
import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import {
  UserGroupIcon,
  EyeIcon,
  CogIcon,
  UsersIcon,
  UserPlusIcon,
  ChevronDownIcon,
  ChevronUpIcon,
  TrashIcon,
  UserIcon,
} from '@heroicons/react/24/outline'
import { Group, useGetGroupQuery, useRemoveGroupMemberMutation } from '@/store/api/groupsApi'
import RoleBadge, { getRolesByGroupType } from './RoleBadge'
import LoadingSpinner from './LoadingSpinner'
import toast from 'react-hot-toast'

interface GroupListCardProps {
  group: Group
  showActions?: boolean
  onEdit?: (group: Group) => void
  onView?: (group: Group) => void
  onAddMembers?: (group: Group) => void
  showAccordion?: boolean
  className?: string
  isPublic?: boolean
  showViewButton?: boolean
}

// Group type display configurations
const groupTypeConfig = {
  CELL: {
    label: 'Cell',
    description: 'Specialized working groups',
    colorClass: 'bg-purple-100 text-purple-800 border-purple-200',
    iconColor: 'text-purple-600',
  },
  COMMITTEE: {
    label: 'Committee',
    description: 'Advisory and decision-making bodies',
    colorClass: 'bg-blue-100 text-blue-800 border-blue-200',
    iconColor: 'text-blue-600',
  },
  OFFICE_BEARERS: {
    label: 'Office Bearers',
    description: 'Executive leadership team',
    colorClass: 'bg-green-100 text-green-800 border-green-200',
    iconColor: 'text-green-600',
  },
  ADVISORS: {
    label: 'Advisors',
    description: 'Advisory and guidance council',
    colorClass: 'bg-orange-100 text-orange-800 border-orange-200',
    iconColor: 'text-orange-600',
  },
}

const GroupListCard: React.FC<GroupListCardProps> = ({
  group,
  showActions = false,
  onEdit,
  onView,
  onAddMembers,
  showAccordion = false,
  className = '',
  isPublic = false,
  showViewButton = true,
}) => {
  const [isAccordionOpen, setIsAccordionOpen] = useState(false)
  const [removingMemberId, setRemovingMemberId] = useState<string | null>(null)

  const typeConfig = groupTypeConfig[group.type]
  const allowedRoles = getRolesByGroupType(group.type)

  // Fetch detailed group data with members when accordion is opened
  const { 
    data: groupDetails, 
    isLoading: isLoadingMembers,
    refetch,
  } = useGetGroupQuery(
    { groupId: group.id, includeMembers: true },
    { skip: !isAccordionOpen || !showAccordion }
  )

  const [removeGroupMember] = useRemoveGroupMemberMutation()

  const members = groupDetails?.members || []

  const handleRemoveMember = async (userId: string, memberName: string) => {
    if (!confirm(`Are you sure you want to remove ${memberName} from ${group.name}?`)) {
      return
    }

    setRemovingMemberId(userId)
    try {
      console.log('Removing member with data:', {
        groupId: group.id,
        userId,
        groupIdLength: group.id.length,
        userIdLength: userId.length,
        groupIdFormat: group.id,
        userIdFormat: userId,
      })
      
      await removeGroupMember({
        groupId: group.id,
        userId,
      }).unwrap()
      
      toast.success(`${memberName} removed from ${group.name}`)
      refetch() // Refresh the member list
    } catch (error: any) {
      console.error('Remove member error:', error)
      console.error('Error details:', {
        status: error?.status,
        data: error?.data,
        message: error?.data?.message,
      })
      toast.error(error?.data?.message || 'Failed to remove member')
    } finally {
      setRemovingMemberId(null)
    }
  }

  const toggleAccordion = () => {
    setIsAccordionOpen(!isAccordionOpen)
  }

  const handleCardClick = (e: React.MouseEvent) => {
    // Prevent card click when clicking on action buttons
    if ((e.target as HTMLElement).closest('.group-actions')) {
      return
    }
    
    if (onView) {
      onView(group)
    }
  }

  return (
    <div 
      className={`bg-white dark:bg-gray-800 rounded-lg shadow-md hover:shadow-lg transition-all duration-200 border border-gray-200 dark:border-gray-700 ${
        onView && !showActions ? 'cursor-pointer hover:border-blue-300 dark:hover:border-blue-600' : ''
      } ${className}`}
      onClick={handleCardClick}
    >
      {/* Card Header */}
      <div className="p-6 pb-4">
        <div className="flex items-start justify-between">
          <div className="flex items-start space-x-4">
            {/* Group Icon */}
            <div className={`p-3 rounded-lg ${typeConfig.colorClass.replace('text-', 'bg-').replace('-800', '-100')} ${typeConfig.colorClass.replace('bg-', 'text-').replace('-100', '-600')}`}>
              <UserGroupIcon className={`w-6 h-6 ${typeConfig.iconColor}`} />
            </div>

            {/* Group Info */}
            <div className="flex-1">
              <div className="flex items-center space-x-3 mb-2">
                <h3 className="text-xl font-bold text-gray-900 dark:text-white">
                  {group.name}
                </h3>
                
                {/* Active/Inactive Status */}
                <div className={`w-2 h-2 rounded-full ${
                  group.isActive ? 'bg-green-400' : 'bg-gray-400'
                }`} title={group.isActive ? 'Active' : 'Inactive'} />
              </div>

              {/* Group Type Badge */}
              <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium border ${typeConfig.colorClass}`}>
                {typeConfig.label}
              </span>
            </div>
          </div>

          {/* Members Count */}
          <div className="text-right flex-shrink-0 ml-4">
            <div className="flex items-center text-gray-600 dark:text-gray-400">
              <UsersIcon className="w-5 h-5 mr-2" />
              <span className="text-2xl font-bold text-gray-900 dark:text-white">
                {group.membersCount}
              </span>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              {group.membersCount === 1 ? 'Member' : 'Members'}
            </p>
          </div>
        </div>

        {/* Description */}
        {group.description && (
          <p className="text-gray-600 dark:text-gray-300 mt-4 leading-relaxed">
            {group.description}
          </p>
        )}
      </div>

      {/* Roles Section */}
      <div className="px-6 pb-4">
        <div className="border-t border-gray-200 dark:border-gray-700 pt-4">
          <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
            Available Roles:
          </h4>
          <div className="flex flex-wrap gap-2">
            {allowedRoles.map((role) => (
              <RoleBadge 
                key={role} 
                role={role} 
                groupType={group.type}
                size="sm"
              />
            ))}
          </div>
        </div>
      </div>

      {/* Actions */}
      {(showActions || !isPublic) && (
        <div className="border-t border-gray-200 dark:border-gray-700 px-6 py-4 group-actions">
          <div className="space-y-3">
            {/* Add Members Button (For admin only) */}
            {showActions && onAddMembers && (
              <button
                onClick={() => onAddMembers(group)}
                className="w-full flex items-center justify-center px-4 py-2 text-sm font-medium text-blue-700 dark:text-blue-400 bg-blue-50 dark:bg-blue-900/20 hover:bg-blue-100 dark:hover:bg-blue-900/40 rounded-lg transition-colors border border-blue-200 dark:border-blue-700"
              >
                <UserPlusIcon className="w-4 h-4 mr-2" />
                Add Members
              </button>
            )}

            <div className="flex space-x-3">
              {/* Manage Button (Priority for admin) */}
              {showActions && onEdit && (
                <button
                  onClick={() => onEdit(group)}
                  className="flex-1 flex items-center justify-center px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-lg transition-colors"
                >
                  <CogIcon className="w-4 h-4 mr-2" />
                  Manage
                </button>
              )}

              {/* View Details Button (Only if not admin or explicitly shown) */}
              {showViewButton && onView && !showActions && (
                <button
                  onClick={() => onView(group)}
                  className="flex-1 flex items-center justify-center px-4 py-2 text-sm font-medium text-blue-700 dark:text-blue-400 bg-blue-50 dark:bg-blue-900/20 hover:bg-blue-100 dark:hover:bg-blue-900/40 rounded-lg transition-colors"
                >
                  <EyeIcon className="w-4 h-4 mr-2" />
                  View Details
                </button>
              )}

              {/* Link to group page for public view */}
              {isPublic && !onView && (
                <Link
                  to={`/groups/${group.id}`}
                  className="flex-1 flex items-center justify-center px-4 py-2 text-sm font-medium text-blue-700 dark:text-blue-400 bg-blue-50 dark:bg-blue-900/20 hover:bg-blue-100 dark:hover:bg-blue-900/40 rounded-lg transition-colors"
                >
                  <EyeIcon className="w-4 h-4 mr-2" />
                  Learn More
                </Link>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Members Accordion - Only show for admin with accordion enabled */}
      {showAccordion && showActions && (
        <div className="border-t border-gray-200 dark:border-gray-700">
          {/* Accordion Header */}
          <button
            onClick={toggleAccordion}
            className="w-full px-6 py-3 flex items-center justify-between text-left hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
          >
            <div className="flex items-center space-x-3">
              <UsersIcon className="w-5 h-5 text-gray-500 dark:text-gray-400" />
              <span className="font-medium text-gray-900 dark:text-white">
                Group Members ({group.membersCount})
              </span>
            </div>
            {isAccordionOpen ? (
              <ChevronUpIcon className="w-5 h-5 text-gray-500 dark:text-gray-400" />
            ) : (
              <ChevronDownIcon className="w-5 h-5 text-gray-500 dark:text-gray-400" />
            )}
          </button>

          {/* Accordion Content */}
          {isAccordionOpen && (
            <div className="border-t border-gray-200 dark:border-gray-700">
              {isLoadingMembers ? (
                <div className="p-6 flex justify-center">
                  <LoadingSpinner size="sm" />
                </div>
              ) : members.length === 0 ? (
                <div className="p-6 text-center">
                  <UserIcon className="w-8 h-8 text-gray-400 mx-auto mb-2" />
                  <p className="text-gray-500 dark:text-gray-400 text-sm">
                    No members in this group yet
                  </p>
                </div>
              ) : (
                <div className="divide-y divide-gray-200 dark:divide-gray-700">
                  {members.map((member) => (
                    <div
                      key={member.id}
                      className="p-4 flex items-center justify-between hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
                    >
                      {/* Member Info */}
                      <div className="flex items-center space-x-3">
                        {/* Profile Photo */}
                        <div className="flex-shrink-0">
                          {member.user.profileImage ? (
                            <img
                              src={`/api/users/profile-picture/${member.user.id}`}
                              alt={member.user.fullName}
                              className="w-10 h-10 rounded-full object-cover"
                              onError={(e) => {
                                const target = e.target as HTMLImageElement
                                target.style.display = 'none'
                                const fallback = target.parentElement?.querySelector('.fallback-avatar') as HTMLElement
                                if (fallback) fallback.style.display = 'flex'
                              }}
                            />
                          ) : null}
                          <div 
                            className={`fallback-avatar w-10 h-10 rounded-full bg-gray-300 dark:bg-gray-600 flex items-center justify-center ${
                              member.user.profileImage ? 'hidden' : ''
                            }`} 
                            style={{ display: member.user.profileImage ? 'none' : 'flex' }}
                          >
                            <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                              {member.user.fullName.charAt(0).toUpperCase()}
                            </span>
                          </div>
                        </div>

                        {/* Member Details */}
                        <div className="flex-1">
                          <div className="flex items-center space-x-3">
                            <h4 className="text-sm font-medium text-gray-900 dark:text-white">
                              {member.user.fullName}
                            </h4>
                            <RoleBadge 
                              role={member.role} 
                              groupType={group.type}
                              size="sm"
                            />
                          </div>
                          <p className="text-xs text-gray-500 dark:text-gray-400">
                            Batch {member.user.batch} • {member.user.email}
                            {!member.isActive && (
                              <span className="ml-2 text-red-500">• Inactive</span>
                            )}
                          </p>
                        </div>
                      </div>

                      {/* Remove Button */}
                      <button
                        onClick={() => handleRemoveMember(member.user.id, member.user.fullName)}
                        disabled={removingMemberId === member.user.id}
                        className="p-2 text-red-600 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        title={`Remove ${member.user.fullName} from group`}
                      >
                        {removingMemberId === member.user.id ? (
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-red-600" />
                        ) : (
                          <TrashIcon className="w-4 h-4" />
                        )}
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>
      )}

      {/* Inactive Overlay */}
      {!group.isActive && (
        <div className="absolute inset-0 bg-gray-500 bg-opacity-20 dark:bg-gray-900 dark:bg-opacity-40 rounded-lg flex items-center justify-center">
          <div className="bg-gray-800 dark:bg-gray-200 text-white dark:text-gray-800 px-4 py-2 rounded-full text-sm font-medium">
            Inactive Group
          </div>
        </div>
      )}
    </div>
  )
}

export default GroupListCard